import React from 'react'

export default class FilteredList extends React.Component {
  render() {
    let optionList
    if (this.props.showOptions && this.props.userInput) {
      if (this.props.filteredOptions.length) {
        optionList = (
          <ul>
            {this.props.filteredOptions.map((optionName, index) => {
              let className;
              if (index === this.props.activeOption) {
                className = "li--active"
              }
              return (
                <li className={className} key={optionName} onClick={this.props.onClick}>
                  {optionName}
                </li>
              )
            })}
          </ul>
        )
      } else {
        optionList = <em>No Results</em>
      }
    }
    return(
      <div className="div--options">
        {optionList}
      </div>
    )
  }
}