import React from "react"
import axios from "axios"

//import recipes from "./recipesDB.json"

// import SearchAndDisplay from "./Components/SearchAndDisplay"
import Autocomplete from './Components/Autocomplete'
import FilteredList from './Components/FilteredList'
import Display from './Components/Display'

const baseUrl = "/api/recipes"

class App extends React.Component {
  state = {
    recipes: [],
    activeOption: 0,
    filteredOptions: [],
    showOptions: false,
    userInput: "",
    title: "",
    ingredients: [],
    description: "",
    type: "",
  }

  componentDidMount() {
    axios.get(baseUrl).then((res) => {
      this.setState({
        recipes: res.data
      })
    })
  }

  onChange = (e) => {
    const options = this.state.recipes.map((recipe) => recipe.title)
    const userInput = e.currentTarget.value
    const filteredOptions = options.filter(
      (option) => option.toLowerCase().indexOf(userInput.toLowerCase()) > -1
    )
    this.setState({
      activeOption: 0,
      filteredOptions,
      showOptions: true,
      userInput,
    })
  }

  onClick = (e) => {
    this.setState({
      activeOption: 0,
      filteredOptions: [],
      showOptions: false,
      userInput: e.currentTarget.innerText
    })
  }

  onKeyDown = (e) => {
    const { activeOption, filteredOptions } = this.state
    if (e.keyCode === 13) {
      this.setState({
        activeOption: 0,
        showSuggestions: false,
        showOptions: false,
        userInput: filteredOptions[activeOption],
        title: "",
        ingredients: [],
        description: "",
        type: ""
      })
    } else if (e.keyCode === 38) {
      if (activeOption === 0) {
        return
      }
      this.setState({ activeOption: activeOption - 1 })
    } else if (e.keyCode === 40) {
      if (activeOption - 1 === filteredOptions.length) {
        return
      }
      this.setState({ activeOption: activeOption + 1 })
    }
  }

  submitInput = (e) => {
    e.preventDefault()
    if (this.state.userInput !== "") {
      const currentRecipe = this.state.recipes.filter(
        (recipe) => recipe.title === this.state.userInput
      )
      if (currentRecipe.length === 1) {
        this.setState({
          userInput: "",
          title: currentRecipe[0].title,
          ingredients: currentRecipe[0].ingredients,
          description: currentRecipe[0].description,
          type: currentRecipe[0].type,
        })
      }
    }
  }

  render() {
    const {
      onChange,
      onClick,
      onKeyDown,
      submitInput,
      state: {
        activeOption,
        filteredOptions,
        showOptions,
        userInput,
        title,
        ingredients,
        description,
        type,
      },
    } = this
    return (
      <div className="div--app">
        <div className="div--app__search">
          <Autocomplete
            onChange={onChange}
            onKeyDown={onKeyDown}
            onClick={submitInput}
            userInput={userInput}
          />
          <FilteredList
            showOptions={showOptions}
            userInput={userInput}
            filteredOptions={filteredOptions}
            onClick={onClick}
            activeOption={activeOption}
          />
        </div>
        <Display
          title={title}
          ingredients={ingredients}
          description={description}
          type={type}
        />
      </div>
    )
  }
}

export default App
